<template>
    <div class="goods">
        <!-- <el-empty v-if="tableData = undefined" description="暂无用户"></el-empty> -->
        <el-table
            :data="pm"
            stripe
            size="mini"
            style="width: 100%">
            <!-- <el-table-column
            prop="date"
            label="日期"
            width="180">
            </el-table-column> -->
            <el-table-column label="orderid" align="center" width="80">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.orderid[scope.$index]"
                />
                <span v-else >{{ scope.row.orderid }}</span>
              </template>
            </el-table-column>
            <el-table-column label="name" align="center" width="80">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.name[scope.$index]"
                />
                <span v-else >{{ scope.row.name }}</span>
              </template>
            </el-table-column>
            <el-table-column label="price" align="center" width="80">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.price[scope.$index]"
                />
                <span v-else >{{ scope.row.price }}</span>
              </template>
            </el-table-column>
            <el-table-column label="description" align="center" width="200">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.description[scope.$index]"
                />
                <span v-else >{{ scope.row.description }}</span>
              </template>
            </el-table-column>
            <el-table-column label="type" align="center" width="80">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.type[scope.$index]"
                />
                <span v-else >{{ scope.row.type }}</span>
              </template>
            </el-table-column>
            <el-table-column label="images" align="center" width="200">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.images[scope.$index]"
                />
                <span v-else >{{ scope.row.images }}</span>
              </template>
            </el-table-column>
            <el-table-column label="userid" align="center" width="80">
              <template slot-scope="scope">
                <el-input
                  v-if="isshow[scope.$index]"
                  type="text"
                  size="mini"
                  clearable
                  v-model="edg.userid[scope.$index]"
                />
                <span v-else >{{ scope.row.userid }}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    v-if="!isshow[scope.$index]"
                    @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
                    <el-button
                      v-if="isshow[scope.$index]"
                      size="mini"
                      type="success"
                      @click="handelCheck(scope.$index,scope.row)"
                    ><i class="el-icon-check"></i>保存</el-button>
                    <el-button
                      v-if="isshow[scope.$index]"
                      size="mini"
                      type="success"
                      @click="handelQx(scope.$index,scope.row)"
                    ><i class="el-icon-close"></i>取消</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
                </template>
            </el-table-column>
        </el-table>
  
        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageg"
        :page-sizes="[6]"
        :page-size="6"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
        </el-pagination>
    </div>
  </template>
  
  <script>
  
  export default {
    data(){
        return{
            tdp: [],
            // tableData:[[]],
            pm:[],
            pageg:1,
            total:null,
            ed:false,
            isshow: [],
            edg:{orderid:[],name:[],price:[],description:[],type:[],images:[],userid:[]},
        }
    },
    methods:{
        handleEdit(index, row) {
            console.log(index, row);
            // this.ed = true
            // this.edg=row.id
            this.$set(this.isshow, index, true);
            this.$set(this.edg.orderid,index,row.orderid);
            this.$set(this.edg.price,index,row.price);
            this.$set(this.edg.description,index,row.description);
            this.$set(this.edg.type,index,row.type);
            this.$set(this.edg.images,index,row.images);
            this.$set(this.edg.userid,index,row.userid);
            this.$set(this.edg.name,index,row.name);
        },
        handelCheck(index,row) {
          this.$set(this.isshow, index, false);
          this.$api.getedgoods({
            orderid:this.edg.orderid[index],name:this.edg.name[index],
            price:this.edg.price[index],description:this.edg.description[index],
            type:this.edg.type[index],images:this.edg.images[index],
            userid:this.edg.userid[index],corderid:row.orderid
          }).then(res=>{})
          // console.log(row.id,this.edg.id[index]);
          // this.$set(row.id,index,this.edg[index])
          row.orderid=this.edg.orderid[index];
          row.name=this.edg.name[index];
          row.price=this.edg.price[index];
          row.description=this.edg.description[index];
          row.type=this.edg.type[index];
          row.images=this.edg.images[index];
          row.userid=this.edg.userid[index];
        },
        handelQx(index,row){
          this.$set(this.isshow, index, false);
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.pm.splice(index,1);
            this.$api.getdelgoods({orderid : row.orderid}).then(res=>{
              console.log(res.data.info);
            })
            this.apidate();
            // this.getd()
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.pageg=val;
            sessionStorage.setItem('pageg',val)
            this.apidate()
        },
        apidate(){
          this.$api.getgoods({page:this.pageg}).then(res=>{
            this.pm=res.data.data
            this.total=res.data.total
          })
        }
    },
    mounted(){
        if(sessionStorage.getItem('pageg')){
            this.pageg=Number(sessionStorage.getItem('pageg'));
        }
        this.apidate()
    },
    created(){
      
    }
  }
  </script>
  
  <style lang="less" scoped>
    .goods{
        margin-top: 70px;
        margin-right: 10px;
    }
    span{
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  </style>
  
    
  